import uniLoadmore from "../../components/uni-load-more/uni-load-more"
export default {
	data() {
		return {
			dataList: '',
		};
	},
	components: {
		uniLoadmore
	},
	computed: {

	},
	methods: {
		link(id){
			uni.navigateTo({
				url: `/pages/extension/detail?id=${id}`
			})
		},
		// 复制文字到剪切板
		setBoard(index) {
			uni.setClipboardData({
				data: this.dataList[index].topic + ' ' + this.dataList[index].content,
				successs: res => {
					uni.showToast({
						title: "已经复制到剪切板"
					})
				},
				fail: err => {
					uni.showToast({
						title: "复制失败",
						icon: 'none'
					})
				}
			})
		},
		// 显示跟多操作
		showModal(index) {
			this.dataList[index].show = !this.dataList[index].show
		},
		// 预览图片
		previewImg(index, index1) {
			uni.previewImage({
				current: index1,
				urls: this.dataList[index].images
			})
		},
		// 保存图片都相册
		downImg(index) {
			for (let a = 0; a < this.dataList[index].images.length; a++) {
				uni.downloadFile({
					url: this.dataList[index].images[a],
					success: res => {
						if (res.statusCode === 200) {
							uni.saveImageToPhotosAlbum({
								filePath: res.tempFilePath,
								success: data => {
									uni.showToast({
										title: "保存成功",
										icon: "none"
									});
								},
								fail: err => {}
							})
						}
					},
					fail: function() {
						uni.showToast({
							title: "保存失败，请稍后重试",
							icon: "none"
						});
					}
				})
			}
		}
	},
	onLoad() {
		this.$store.dispatch('getNewsList', {}).then(res => {
			this.dataList = res.data.map(el => {
				return Object.assign(el, {
					show: false
				})
			})
		})
	}
}
